import {inject, InjectionToken} from '@angular/core';
import {AUDIO_CONTEXT} from '@ng-web-apis/audio';

/**
 * Stackblitz does not support audio assets so we have to encode it in base64
 */
const RESPONSE = ``;

export const RESPONSE_BUFFER = new InjectionToken<Promise<AudioBuffer>>('Response', {
    factory: () => inject(AUDIO_CONTEXT).decodeAudioData(decode(RESPONSE)),
});

function decode(base64: string): ArrayBuffer {
    const binary_string = window.atob(base64);
    const len = binary_string.length;
    const bytes = new Uint8Array(len);

    for (let i = 0; i < len; i++) {
        bytes[i] = binary_string.charCodeAt(i);
    }

    return bytes.buffer;
}
